@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Nothing+You+Could+Do&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;469;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.fence{
  background-image: url(/react-portfolio/static/media/fence.b3589f32.jpg);
}

.wrapper {
  background-image: url(/react-portfolio/static/media/fence.b3589f32.jpg);
  /* height: 10000px; */
}

.hero-body {
  margin: 75px;
  padding: 50px;
}


.container {
  min-height: 100%;
}

.font {
  font-family: 'Oswald', sans-serif;
}

.foot-font {
  font-size: 24px;
  font-family: 'Amatic SC', cursive;
}

.navbar-font {
  font-size: 24px;
  font-family: 'Amatic SC', cursive;
}

.nav-link-margin {
  margin-right: 15px;
}

.project-font {
  font-size: 20px;
}

.large-font {
  font-size: 55px;
}

.med-font {
  font-size: 24px;
}

.title-text {
  font-size: 75px;
  font-weight: bold;
}

.white-font {
  color: white;
}

.bold {
  font-weight: bold;
}

.margin-auto {
  margin: auto;
}

.center {
  text-align: center;
}

.underline {
  text-decoration: underline;
}

.main-margin {
  margin-top: 40px;
  font-size: 20px;
  font-weight: bold;
}

.portfolio-image {
  width: 400px;
  border: 1px solid white;
}

.main-header {
  margin-top: 50px;
  margin-bottom: 50px !important;
}

.brand-statement {
  text-align: justify;
}

.link-margin {
  margin-top: 20px;
}
.port-list {
  margin-top: 30px;
}

.ul-style {
  list-style-type: circle;
}

.project-margin {
  margin-left: 20px;
}

.column-margin {
  margin-top: 40px;
  border-top: 1px solid white;
}

.form-margin {
  margin-top: 40px;
  border: 1px solid white;
  padding: 100px;
}

.nav-margin {
  margin-left: 10px;
  margin-right: 10px;
  width: 100%;
}

.large-margin {
  padding-bottom: 150px;
}

.foot {
  padding-top: 25px;
  padding-bottom: 20px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #1a2a2d;
}

